body {
  color: white;
  background-color: #21232A;
  padding: 2rem;
}

* {
  font-family: 'Georama', sans-serif;
}

input {
  font-size: 1.2rem;
  width: 100%;
  color: grey;
  border-radius: 4px;
  outline: none;
  padding: 0.5rem;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  padding-top: 1.2rem;
  text-align: center;
  border: 1px solid grey;
  border-radius: 5px;
  margin: 0 10px 10px 0;
}

.card img {
  max-height: 170px;
}